.nav-bar {
  height: 30vh;
  background: url(/static/media/lib.e6aa5c3a.jpg);
  background-position: top;
  background-position-y: -150px;

  background-size: cover;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.233);
}

.navLink {
  font-family: "Spartan";
  color: #ffffff;
  text-shadow: 2px 2px 2px rgb(14, 13, 13);
  font-weight: bold;
  font-size: 28px;
  z-index: 10;
}

@media only screen and (max-width: 600px) {
  .nav-bar {
    height: 20vh;
    background-position-y: 0;
  }

  .navLink {
    font-size: 20px;
    text-align: center;
  }
}

.search-box {
  margin-top: 6vh;
  align-items: center;
}

.bookQuote {
  margin-top: 10px;
  font-size: 16px;
  font-style: italic;
  color: #757474;
  font-family: Spartan;

  padding: 10px;
}

.search-label {
  font-family: "Spartan";
  font-size: 25px;
  letter-spacing: 0px;
}

.search-button {
  border: 2px solid #ff0a87;
  box-shadow: 0px 2px 4px rgba(121, 121, 121, 0.616);
  background-color: inherit;
  font-weight: 600;
  border-radius: 20px;
  font-size: 14px;

  vertical-align: middle;

  font-family: "Spartan";
}

.fa-search {
  font-size: 1.2em;
  color: #ff0a87;
  padding: 5px;
  text-align: left;
}

.search-button:hover {
  background-color: #ff0a8960;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.input-row input {
  background-color: white;
}

input[type="text"] {
  background: #f1f1f1;
}

@media only screen and (max-width: 600px) {
  .search-label {
    font-family: "Spartan";
    font-size: 18px;
  }

  .search-button {
    margin-top: 5px;
    margin: auto;
  }
  .bookQuote {
    font-size: 13px;
  }
}

.book-in-db {
  font-family: "Spartan";
  color: #363636;
  text-align: center;
  font-size: 14px;
}
.book-card-container h6 {
  font-family: "Spartan";
  font-size: 14px;
  color: #363636;
}
.hide {
  display: none;
}

.show {
  display: block;
}
.book-card-container h5 {
  font-weight: 900;
  font-family: "Spartan";
  font-size: 16px;
  color: #363636;
  text-align: center;
  letter-spacing: 0;
  margin-top: 5px;
}
.sub-title {
  text-align: center;
  font-family: "Spartan";
}
.book-cover {
  border-radius: 5px;
  box-shadow: 0px 1px 5px rgb(105, 105, 105);
}

.book-card-container {
  margin-top: 15px;
  margin-right: 25px;
  justify-content: space-evenly;
}

.book-card {
  padding: 10px;
  margin: 10px 40px;
  border: 1px solid #adadad9f;
  background-color: rgb(246, 246, 246);
  box-shadow: 0px 4px 5px rgba(5, 5, 5, 0.383);
}

.fa-heart {
  cursor: pointer;
}

.fa-heart:hover {
  color: red;
}

.modal-body-bookCard {
  font-family: "Caladea";
  font-size: 14px !important;
  letter-spacing: 0.5px;
  padding: 20px;
}

.modal-header-bookCard {
  padding: 5px !important;
}

